import { POST_TYPES } from "./types";
import _g from "./g";
import utilsFactory from "./utils";
import fetch from 'cross-fetch';
export default function authFactory(globals) {
    const g = globals || _g;
    const { generateBareUrl, generateAuthBody, log } = utilsFactory(g);
    const getUserAttributes = async () => {
        try {
            const attrsRes = await tokenPost(POST_TYPES.USER_ATTRIBUTES);
            return attrsRes.data;
        }
        catch (error) {
            return error;
        }
    };
    const setUserAttribute = async (key, value) => {
        try {
            const setAttrsRes = await tokenPost(POST_TYPES.SET_ATTRIBUTE, {
                key,
                value
            });
            return {
                success: setAttrsRes.success,
                message: JSON.stringify(setAttrsRes.data)
            };
        }
        catch (error) {
            return {
                success: false,
                message: "Error",
                error
            };
        }
    };
    const signUp = async (newUserID, password, userAttributes) => {
        try {
            const signUpRes = await tokenPost(POST_TYPES.SIGN_UP, {
                newUserID,
                password,
                userAttributes
            });
            return {
                success: signUpRes.success,
                message: signUpRes.data
            };
        }
        catch (error) {
            return {
                success: false,
                message: "Error",
                error
            };
        }
    };
    const signIn = async (userID, password) => {
        const t1 = Date.now();
        g.session = Math.floor(100000000 + Math.random() * 900000000);
        const integrationType = g.ebconfig.integration.split("-")[0].toUpperCase() === "PROJECT" ? "PROJECT" : "REACT";
        try {
            const res = await fetch(generateBareUrl(integrationType, g.integrationID), {
                method: "POST",
                headers: {
                    'Eb-Post-Req': POST_TYPES.HANDSHAKE,
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    version: g.ebconfig.version,
                    session: g.session,
                    instance: g.instance,
                    userID,
                    password
                })
            });
            const resData = await res.json();
            if (resData.token) {
                g.token = resData.token;
                g.refreshToken = resData.refreshToken;
                g.newTokenCallback();
                g.mounted = true;
                const validTokenRes = await tokenPost(POST_TYPES.VALID_TOKEN);
                const elapsed = Date.now() - t1;
                if (validTokenRes.success) {
                    log("Valid auth initiation in " + elapsed + "ms");
                    return {
                        success: true,
                        message: "Successfully signed in user"
                    };
                }
                else {
                    return {
                        success: false,
                        message: "Could not sign in user"
                    };
                }
            }
            else {
                return {
                    success: false,
                    message: "Could not sign in user"
                };
            }
        }
        catch (error) {
            console.error(error);
            return {
                success: false,
                message: error,
                error
            };
        }
    };
    const isUserSignedIn = () => g.token.length > 0;
    const signOut = () => {
        g.token = "";
        g.newTokenCallback();
    };
    const initAuth = async () => {
        const t1 = Date.now();
        g.session = Math.floor(100000000 + Math.random() * 900000000);
        log(`Handshaking on${g.instance} instance`);
        const integrationType = g.ebconfig.integration.split("-")[0].toUpperCase() === "PROJECT" ? "PROJECT" : "REACT";
        try {
            const res = await fetch(generateBareUrl(integrationType, g.integrationID), {
                method: "POST",
                headers: {
                    'Eb-Post-Req': POST_TYPES.HANDSHAKE,
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    version: g.ebconfig.version,
                    tt: g.ebconfig.tt,
                    session: g.session,
                    instance: g.instance
                })
            });
            const resData = await res.json();
            if (resData.token) {
                g.token = resData.token;
                g.mounted = true;
                const validTokenRes = await tokenPost(POST_TYPES.VALID_TOKEN);
                const elapsed = Date.now() - t1;
                if (validTokenRes.success) {
                    log("Valid auth initiation in " + elapsed + "ms");
                    return true;
                }
                else {
                    return false;
                }
            }
            else {
                return false;
            }
        }
        catch (error) {
            console.error(error);
            return false;
        }
    };
    const tokenPost = async (postType, body) => {
        if (!g.mounted) {
            await initAuth();
        }
        const integrationType = g.ebconfig.integration.split("-")[0].toUpperCase() === "PROJECT" ? "PROJECT" : "REACT";
        try {
            const res = await fetch(generateBareUrl(integrationType, g.integrationID), {
                method: "POST",
                headers: {
                    'Eb-Post-Req': postType,
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    _auth: generateAuthBody(),
                    ...body
                })
            });
            const resData = await res.json();
            if ({}.hasOwnProperty.call(resData, 'ErrorCode') || {}.hasOwnProperty.call(resData, 'code')) {
                if (resData.code === "JWT EXPIRED") {
                    if (integrationType === "PROJECT") {
                        const req_res = await tokenPost(POST_TYPES.REQUEST_TOKEN, {
                            refreshToken: g.refreshToken,
                            token: g.token
                        });
                        if (req_res.success) {
                            g.token = req_res.data.token;
                            g.newTokenCallback();
                            return tokenPost(postType, body);
                        }
                        else {
                            g.token = "";
                            g.refreshToken = "";
                            g.newTokenCallback();
                            return {
                                success: false,
                                data: req_res.data
                            };
                        }
                    }
                    else {
                        await initAuth();
                    }
                    return tokenPost(postType, body);
                }
                return {
                    success: false,
                    data: resData.body
                };
            }
            else {
                return {
                    success: resData.success,
                    data: resData.body
                };
            }
        }
        catch (error) {
            return {
                success: false,
                data: error
            };
        }
    };
    const tokenPostAttachment = async (formData, customHeaders) => {
        if (!g.mounted) {
            await initAuth();
        }
        const regularAuthbody = generateAuthBody();
        const attachmentAuth = {
            'Eb-token': regularAuthbody.token,
            'Eb-token-time': regularAuthbody.token_time,
            'Eb-now': regularAuthbody.now
        };
        const integrationType = g.ebconfig.integration.split("-")[0].toUpperCase() === "PROJECT" ? "PROJECT" : "REACT";
        try {
            const res = await fetch(generateBareUrl(integrationType, g.integrationID), {
                method: "POST",
                headers: {
                    'Eb-Post-Req': POST_TYPES.UPLOAD_ATTACHMENT,
                    ...customHeaders,
                    ...attachmentAuth
                },
                body: formData
            });
            const resData = await res.json();
            if ({}.hasOwnProperty.call(resData, 'ErrorCode') || {}.hasOwnProperty.call(resData, 'code')) {
                if (resData.code === "JWT EXPIRED") {
                    if (integrationType === "PROJECT") {
                        const req_res = await tokenPost(POST_TYPES.REQUEST_TOKEN, {
                            refreshToken: g.refreshToken,
                            token: g.token
                        });
                        if (req_res.success) {
                            g.token = req_res.data.token;
                            g.newTokenCallback();
                            return tokenPostAttachment(formData, customHeaders);
                        }
                        else {
                            g.token = "";
                            g.refreshToken = "";
                            g.newTokenCallback();
                            return {
                                success: false,
                                data: req_res.data
                            };
                        }
                    }
                    else {
                        await initAuth();
                    }
                    return tokenPostAttachment(formData, customHeaders);
                }
                return {
                    success: false,
                    data: resData.body
                };
            }
            else {
                return {
                    success: resData.success,
                    data: resData.body
                };
            }
        }
        catch (error) {
            return {
                success: false,
                data: error
            };
        }
    };
    return {
        initAuth,
        tokenPost,
        tokenPostAttachment,
        signUp,
        setUserAttribute,
        getUserAttributes,
        isUserSignedIn,
        signIn,
        signOut
    };
}
